import './App.css';
import TitleBar from './components/TitleBar/TitleBar.component';
import DonePage from './components/DonePage/DonePage.components';
import HomePage from './components/HomePage/HomePage.component';
import React, { Component } from 'react';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      done: false
    };
    this.changePage = this.changePage.bind(this);
  }

  changePage() {
    this.setState({
      done: !this.state.done
    });
  }

  render() {
    return (
      <div className="App">
        <TitleBar />
        <div>
          {this.state.done ? (
            <DonePage changePage={this.changePage} />
          ) : (
            <HomePage changePage={this.changePage} />
          )}
        </div>
      </div>
    );
  }
}

export default App;
