import React, { Component } from 'react';
import FormInput from '../form-input/form-input.component';
import FormRadio from '../form-radio/form-radio.component';
import { ReactComponent as HomeSVG } from '../../assets/home-svg.svg';
import './HomePage.styles.scss';
import ButtonRound from '../button-round/button-round.component';

class HomePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      angkatan: null,
      nim: '',
      jurusan: '',
      universitas: '',
      incorrect: false,
      incData: ''
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleClose = this.handleClose.bind(this);
  }

  handleChange(evt) {
    this.setState({
      [evt.target.name]: evt.target.value
    });
  }

  handleClose() {
    this.setState({
      incorrect: false
    });
  }

  handleSubmit(evt) {
    const { name, email, jurusan, universitas, nim, angkatan } = this.state;

    let incData = '';
    let error = false;

    if (!name) {
      error = true;
      incData = 'Nama Anda Kosong';
    }

    if (!jurusan) {
      error = true;
      incData = 'Jurusan Anda Kosong';
    }

    if (!universitas) {
      error = true;
      incData = 'Universitas Anda Kosong';
    }

    if (!nim) {
      error = true;
      incData = 'Nomor Induk Anda Kosong';
    }

    if (!angkatan) {
      error = true;
      incData = 'Angkatan Anda Kosong';
    }

    if (!email) {
      error = true;
      incData = 'Email Anda Kosong';
    } else {
      const re = /\S+@\S+/;

      if (re.test(String(email).toLowerCase()) === false) {
        error = true;
        incData = 'Email Anda Salah';
      }
    }

    if (error) {
      this.setState({
        incorrect: error,
        incData: incData
      });
    } else {
      this.props.changePage();
    }
  }

  render() {
    const {
      name,
      email,
      incData,
      jurusan,
      universitas,
      nim,
      incorrect
    } = this.state;
    return (
      <div className="homepage">
        <h1 className="homepage__title">Form Biodata Diri</h1>
        <p>
          Isi form biodata di bawah ini sesuai dengan format yang sudah
          ditentukan.
        </p>
        <div className="homepage__content">
          <form className="homepage__form" onSubmit={this.handleSubmitSignUp}>
            <FormInput
              name="name"
              type="name"
              label="Nama"
              value={name}
              placeholder="Full Name"
              handleChange={this.handleChange}
              required
            />
            <FormInput
              name="email"
              type="email"
              label="Email"
              value={email}
              placeholder="Alamat Email"
              handleChange={this.handleChange}
              required
            />
            <FormInput
              name="jurusan"
              type="jurusan"
              label="Jurusan"
              value={jurusan}
              placeholder="Jurusan"
              handleChange={this.handleChange}
              required
            />
            <FormInput
              name="nim"
              type="nim"
              label="Nomor Induk Mahasiswa"
              value={nim}
              placeholder="Nomor Induk Mahasiswa"
              handleChange={this.handleChange}
              required
            />
            <FormInput
              name="universitas"
              type="universitas"
              label="Universitas"
              value={universitas}
              placeholder="Universitas"
              handleChange={this.handleChange}
              required
            />
            <FormRadio
              name="angkatan"
              items={[2020, 2019, 2018, 2017]}
              handleChange={this.handleChange}
              label="Angkatan"
              required
            />
          </form>

          <div className="homepage__svg">
            <HomeSVG className="home-svg" />
            <ButtonRound onClick={this.handleSubmit}>Submit Form</ButtonRound>
          </div>
        </div>
        {incorrect ? (
          <div className="homepage__warning">
            <i class="fas fa-exclamation-triangle"></i>
            <p>{incData}</p>
            <button onClick={this.handleClose}>Close</button>
          </div>
        ) : (
          ''
        )}
      </div>
    );
  }
}

export default HomePage;
