import { ReactComponent as DoneSVG } from '../../assets/done-svg.svg';
import React, { Component } from 'react';
import ButtonRound from '../button-round/button-round.component';

import './DonePage.styles.scss';

class DonePage extends Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    this.props.changePage();
  }

  render() {
    return (
      <div className="donepage">
        <h1>Thanks For Submitting</h1>
        <div className="donepage__svg">
          <DoneSVG />
        </div>
        <p>Anda dapat mengisi form biodata kembali.</p>
        <ButtonRound onClick={this.handleClick}>
          Fill the Form Again
        </ButtonRound>
      </div>
    );
  }
}

export default DonePage;
